/* Main section styling */
.main-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 4rem 1.5rem;
    background-color: #00BFFF;
    background: linear-gradient(90deg, #02ade5(9, 13, 18) 13%, #1dbaef(17, 22, 28) 58%);
  }
  
  /* Text section styling */
  .text-section {
    width: 50%;
    padding-right: 2rem;
  }
  
  .headline {
    font-size: 2.25rem;
    font-weight: 700;
    color: #ffffff;
    margin-bottom: 1rem;
  }
  
  .description {
    font-size: 1.125rem;
    color: #ffffff;
    margin-bottom: 1.5rem;
  }
  
  .cta-button {
    background-color: #3182ce;
    color: white;
    padding: 0.75rem 1.5rem;
    border-radius: 0.375rem;
    font-size: 1.125rem;
    text-decoration: none;
  }
  
  .cta-button:hover {
    background-color: #2b6cb0;
  }
  
  /* Image section styling */
  .image-section {
    width: 50%;
  }
  
  .image {
    width: 100%;
    height: auto;
    border-radius: 0.375rem;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  